
import { useMemo, useState } from "react";
import styles from './index.less'
// æ€è·¯ ä¼ å…¥ä¸€ä¸ªæ•°ç»„ï¼Œæ•°ç»„çš„å†…å®¹çš„é•¿åº¦å°±æ˜¯ å…‰æ ‡çš„é•¿åº¦ é€šè¿‡å…‰æ ‡çš„ä½ç½®æ‰¾åˆ° è¦åˆ é™¤æ•°ç»„çš„index

// åˆ é™¤çš„æ—¶å€™ï¼Œ ç»„ä»¶æ›´æ–°ï¼Œå…‰æ ‡ä¸åœ¨è®¾ç½®ä½ç½®ï¼Œéœ€è¦
enum KeyBoardCode {
    DELETE = 'Backspace',
    LEFT = 'ArrowLeft',
    RIGHT = 'ArrowRight'
}

const CursorInput = (props: any) => {

    const {selectedOptions, setSelectedOptions, placeHolder="è¯·é€‰æ‹©"} = props;

    const getValue = useMemo(() => {
        let str = '';
        selectedOptions.forEach((item: { content: any; }) => {
            str += `${item.content};`
        })
        return str;
    }, [selectedOptions])


    const handleKeyDown = (e: any) => {

        if (e.key === KeyBoardCode.DELETE) {
            const target = e.currentTarget as HTMLInputElement;
            const start = target.selectionStart || 0;  // å…‰æ ‡å¼€å§‹çš„ä½ç½®

            let currentIndex = 0;
            let lastIndex = 0;
            let deleIndex = -1;
            for (let i = 0; i < selectedOptions.length; i++) {
                let index = selectedOptions[i].content.length + 1;
                currentIndex += index;
                if (start > lastIndex && start <= currentIndex) {
                    deleIndex = i;
                    break;
                }
                lastIndex += index;
            }
            if (deleIndex > -1) {
                const filter = selectedOptions.filter((item: any, index: number) => deleIndex !== index);
                setSelectedOptions(filter)
                // éœ€è¦ç­‰æ¸²æŸ“ç½‘ selectedOptions // å…‰æ ‡å›åˆ°ä½ç½®
                setTimeout(() => {
                    target.setSelectionRange(lastIndex, lastIndex)
                }, 0)

            }
        }

        if (e.key === KeyBoardCode.LEFT || e.key === KeyBoardCode.RIGHT) {
            const target = e.currentTarget as HTMLInputElement;
            const start = target.selectionStart || 0;
            let currentIndex = 0;
            let lastIndex = 0;
            let toIndex = 0;

            for (let i = 0; i < selectedOptions.length; i++) {
                let index = selectedOptions[i].content.length + 1;
                currentIndex += index;

                // å…‰æ ‡åœ¨0çš„ä½ç½®  å…‰æ ‡è¦åˆ°å°¾éƒ¨ left
                if (start === 0 && e.key === KeyBoardCode.LEFT) {
                    toIndex = selectedOptions.reduce((re: any, cur: { content: string | any[]; }) => re + cur.content.length + 1, 0);
                    break;
                }
                // 0 right 
                if (start === 0 && e.key === KeyBoardCode.RIGHT) {
                    toIndex = currentIndex;
                    console.log(toIndex, 'ppp');

                    break;
                }
                // å…‰æ ‡å­—åœ¨ä¸­é—´
                if (start >= lastIndex && start < currentIndex) {
                    if (e.key === KeyBoardCode.LEFT) {
                        toIndex = lastIndex;
                    } else {
                        toIndex = currentIndex;
                    }
                    break;
                }

                //åœ¨ï¼›åé¢
                if (start === currentIndex) {
                    if (e.key === KeyBoardCode.LEFT) {
                        toIndex = lastIndex;
                    } else {
                        const nextOption = selectedOptions[i + 1];
                        if (nextOption) {
                            toIndex = currentIndex + nextOption.content.length + 1;
                        }
                    }
                    break;
                }
                lastIndex += index
            }


            const where = toIndex

            target.setSelectionRange(where, where);
            target.blur();
            target.focus();


        }
        //  é˜»æ­¢é»˜è®¤äº‹ä»¶ å› ä¸ºå®ƒéƒ½ä¼šè¿›ä¸€ä½
        e.preventDefault()
    }
    // console.log(selectedOptions);

    return (
        <div>
            <input
                type="text"
                value={getValue}
                onKeyDown={handleKeyDown}
                className={styles.input}
                onChange={() =>{}}
                placeholder={placeHolder}
            />
        </div>
    )

}

export default CursorInput;
